<!--
 * @Description: vueBaiDuMap绘制自定义折线 弹窗 页面
 * @Author: mhf
 * @Date: 2023-04-27 11:37:11
-->

<template>
  <el-dialog
    title="绘制折线"
    width="43%"
    :visible.sync="dialogVisible"
    :before-close="hideDialog"
    :close-on-click-modal="false"
  >
    <div class="dialog-body">
      <baidu-map
        class="map"
        :center="mapCenter"
        :zoom="mapZoom"
        :scroll-wheel-zoom="true"
        @mousemove="syncPolyline"
        @click="paintPolyline"
        @rightclick="newPolyline"
      >
        <bm-control>
          <el-button size="small" type="danger" @click="toggle('polyline')">{{
            polyline.editing ? "停止绘制" : "开始绘制"
          }}</el-button>
        </bm-control>
        <bm-polyline
          :path="path"
          v-for="(path, index) of polyline.paths"
          :key="index"
        ></bm-polyline>
      </baidu-map>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="hideDialog">取 消</el-button>
      <el-button type="primary" @click="hideDialog">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: "drawAreaDialog",
  components: {},
  props: {},
  data() {
    return {
      dialogVisible: false,
      mapCenter: { lng: 119.93344129237624, lat: 28.464762954747187 },
      mapZoom: 13,
      polyline: {
        editing: false,
        paths: [],
      },
    };
  },
  methods: {
    /**
     * @Event 方法
     * @description: 关闭弹窗
     * */
    hideDialog() {
      this.dialogVisible = false;
    },

    /**
     * @Event 方法
     * @description: 打开弹窗
     * */
    showDialog() {
      this.dialogVisible = true;
    },

    toggle(name) {
      this[name].editing = !this[name].editing;
    },
    syncPolyline(e) {
      if (!this.polyline.editing) {
        return;
      }
      const { paths } = this.polyline;
      if (!paths.length) {
        return;
      }
      const path = paths[paths.length - 1];
      if (!path.length) {
        return;
      }
      if (path.length === 1) {
        path.push(e.point);
      }
      this.$set(path, path.length - 1, e.point);
    },
    newPolyline(e) {
      if (!this.polyline.editing) {
        return;
      }
      const { paths } = this.polyline;
      if (!paths.length) {
        paths.push([]);
      }
      const path = paths[paths.length - 1];
      path.pop();
      if (path.length) {
        paths.push([]);
      }
    },
    paintPolyline(e) {
      if (!this.polyline.editing) {
        return;
      }
      const { paths } = this.polyline;
      !paths.length && paths.push([]);
      paths[paths.length - 1].push(e.point);
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
.dialog-body {
  width: 100% !important;
  height: 500px !important;

  .map {
    width: 100% !important;
    height: 100% !important;
  }
}
</style>
