<!--
 * @Description: vueBaiDuMap 使用 页面
 * @Author: mhf
 * @Date: 2023-04-27 10:44:08
-->
<template>
  <div class="map">
    <baidu-map
      class="map"
      :center="mapCenter"
      :zoom="mapZoom"
      :scroll-wheel-zoom="true"
      @ready="onMapReady"
      @click="onMapClick($event)"
    >
      <!-- 定位的控件 -->
      <bm-geolocation
        anchor="BMAP_ANCHOR_BOTTOM_RIGHT"
        :showAddressBar="true"
        :autoLocation="true"
        @locationSuccess="handleLocationSuccess"
      />
      <!-- 定位的控件 -->

      <!-- 点聚合 -->
      <bml-marker-clusterer :averageCenter="true">
        <!-- 点 -->
        <bm-marker
          v-for="(item, index) in markerPoints"
          :key="index"
          :position="item"
          :icon="markerIcon"
          @click="markerClick(item, index)"
        >
        </bm-marker>
        <!-- 点 -->

        <!-- 你的位置 -->
        <bm-marker :position="locationPoint" @click="infoWindowOpen">
        </bm-marker>
        <bm-info-window
          :show="showInfoWindow"
          :position="locationPoint"
          :offset="{ width: 8, height: -20 }"
          @close="infoWindowClose"
          @open="infoWindowOpen"
        >
          你的位置
        </bm-info-window>
        <!-- 你的位置 -->
      </bml-marker-clusterer>
      <!-- 点聚合 -->

      <!-- 多边形 -->
      <bm-polygon
        v-for="(item, index) in polygonPath"
        :key="item.toString()"
        :path="item"
        stroke-color="#1492FF"
        fill-color="#1492FF"
        :fill-opacity="0.2"
        :stroke-opacity="1"
        :stroke-weight="3"
      />
      <!-- 多边形 -->

      <!-- 自定义控件 -->
      <bm-control>
        <el-button
          style="margin: 20px 0 0 20px"
          type="primary"
          size="small"
          @click="showDrawPolylineDialog"
          >绘制折线
        </el-button>
        <el-button
          style="margin: 20px 0 0 20px"
          type="primary"
          size="small"
          @click="showDrawAreaDialog"
          >绘制多边形弹窗
        </el-button>
        <el-button
          style="margin: 20px 0 0 20px"
          type="primary"
          size="small"
          @click="drawArea"
          >{{ isDraw ? "删除多边形" : "绘制多边形" }}
        </el-button>
        <el-button
          style="margin: 20px 0 0 20px"
          type="primary"
          size="small"
          @click="getVisibleAreaPointsBtn"
          >可视区坐标
        </el-button>
        <el-button
          style="margin: 20px 0 0 20px"
          type="primary"
          size="small"
          @click="getMapZoom"
          >地图缩放层级
        </el-button>
      </bm-control>
    </baidu-map>
    <!-- 自定义控件 -->

    <!-- 弹窗 -->
    <el-dialog
      title="点位"
      :visible.sync="dialogVisible"
      width="39%"
      :before-close="hideDialog"
    >
      <div>
        {{ dialogData }}
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="hideDialog">取 消</el-button>
        <el-button type="primary" @click="hideDialog">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 弹窗 -->

    <drawPolylineDialog ref="drawPolylineDialog" />
    <drawAreaDialog ref="drawAreaDialog" />
  </div>
</template>

<script>
import drawPolylineDialog from "@/views/vueBaiduMap/drawPolylineDialog";
import drawAreaDialog from "@/views/vueBaiduMap/drawAreaDialog";
import { BmlMarkerClusterer } from "vue-baidu-map";

export default {
  name: "vueBaiDuMap",
  components: { drawPolylineDialog, drawAreaDialog, BmlMarkerClusterer },
  props: {},
  data() {
    return {
      mapCenter: { lng: 119.93344129237624, lat: 28.464762954747187 },
      mapZoom: 13,
      markerPoints: [
        // {
        //     id: 1,
        //     name: "天安门",
        //     address: "北京市东城区东长安街",
        //     lng: "116.397515",
        //     lat: "39.908556",
        // },
        // {
        //     id: 2,
        //     name: "故宫博物馆",
        //     address: "北京市东城区景山前街4号",
        //     lng: "116.403369",
        //     lat: "39.924169",
        // },
      ], // 存放点位数据, 数据格式如上（id，lng，lat必须）
      points: require("./ponits.json").data, // 模拟接口数据
      markerIcon: {
        url: require("../../../public/img/forklift.png"),
        size: {
          width: 36,
          height: 36,
        },
      }, // 点位图标
      dialogVisible: false, // 是否展示弹窗
      dialogData: null, // 弹窗中展示的数据

      polygonPath: [], // 存放多边形数据
      isDraw: false, // 绘制多边形/删除多边形
      map: null, // 地图实例
      visibleAreaPoints: {}, // 可视区坐标
      locationPoint: {}, // 你的位置
      showInfoWindow: true, // 是否展示消息窗体
    };
  },
  methods: {
    /**
     * @Event 方法
     * @description: 定位成功后触发此事件
     * */
    handleLocationSuccess(e) {
      console.log(e);
    },

    /**
     * @Event 方法
     * @description: 点位点击事件
     * */
    markerClick(data, index) {
      // console.log(data)
      this.dialogVisible = true;
      // this.dialogData = data // 1
      this.dialogData = this.markerPoints[index]; // 2
    },

    /**
     * @Interface 接口
     * @description: 模拟数据请求 获取点位数据
     * */
    getPoints() {
      // console.log(this.points)
      this.points.forEach((item, index) => {
        this.markerPoints.push({
          id: item.clusterId,
          lng: item.x,
          lat: item.y,
        });
      });
    },

    /**
     * @Event 方法
     * @description: 获取地图缩放层级
     * */
    getMapZoom() {
      this.$message.success(this.map.getZoom());
    },

    /**
     * @Event 方法
     * @description: 关闭弹窗
     * */
    hideDialog() {
      this.dialogVisible = false;
    },

    /**
     * @Event 方法
     * @description: 打开绘制折线弹窗
     * */
    showDrawPolylineDialog() {
      this.$refs.drawPolylineDialog.showDialog();
    },

    /**
     * @Event 方法
     * @description: 打开绘制区域弹窗
     * */
    showDrawAreaDialog() {
      this.$refs.drawAreaDialog.showDialog();
    },

    /**
     * @Event 方法
     * @description: 绘制区域
     * */
    drawArea() {
      this.isDraw = !this.isDraw;
      this.polygonPath = this.isDraw ? require("./areaPoints.json").data : [];
    },

    /**
     * @Event 方法
     * @description: 获取地图实例，并添加事件监听器
     * */
    onMapReady(map) {
      console.log(map);
      this.map = map.map;
      // console.log(map);
      this.map.addEventListener("dragend", this.getVisibleAreaPoints); // 停止拖拽地图时触发
      this.map.addEventListener("zoomend", this.getVisibleAreaPoints); // 地图更改缩放级别结束时触发触发此事件

      /* 自动定位 */
      console.log(map.BMap);
      let _this = this; // 设置一个临时变量指向vue实例，因为在百度地图回调里使用this，指向的不是vue实例；
      var geolocation = new map.BMap.Geolocation();
      geolocation.getCurrentPosition(
        function (r) {
          _this.mapCenter = { lng: r.longitude, lat: r.latitude }; // 设置center属性值
          _this.locationPoint = { lng: r.longitude, lat: r.latitude }; // 自定义覆盖物
          _this.mapZoom = 18;
          _this.showInfoWindow = true;
          console.log("center:", _this.mapCenter); // 如果这里直接使用this是不行的
        },
        { enableHighAccuracy: true }
      );
      // 下面注释是百度地图API官方实现方法，因为我使用自定义图标覆盖物，所以没有使用这种方法！
      // 如使用以下这种方法，那么我Template里所写的自定义定位图标代码是不需要的
      // var geolocation = new BMap.Geolocation();
      // geolocation.getCurrentPosition(function(r){
      // if(this.getStatus() == BMAP_STATUS_SUCCESS){
      //   var mk = new BMap.Marker(r.point);
      //   map.addOverlay(mk);
      //   map.panTo(r.point);
      //   alert('您的位置：'+r.point.lng+','+r.point.lat);
      // }
      // else {
      //   alert('failed'+this.getStatus());
      // }
      // },{enableHighAccuracy: true})
    },

    /**
     * @Event 方法
     * @description: 地图点击并标记点位
     * */
    onMapClick(data) {
      this.markerPoints.push(data.point); // 点击新增点位

      let address,
        title = "";
      let resData = {};
      // 逆地址解析
      let point = new window.BMap.Point(data.point.lng, data.point.lat);
      const geoc = new window.BMap.Geocoder();
      geoc.getLocation(point, (rs) => {
        console.log(rs);
        address = rs.address.toString();
        title =
          rs.surroundingPois.length > 0
            ? rs.surroundingPois[0].title.toString()
            : "";
        resData.address = address + title;
      });
      resData.point = data.point;
      console.log("resData:", resData);
    },

    /**
     * @Event 方法
     * @description: 获取可视区坐标
     * */
    getVisibleAreaPoints() {
      // 当地图拖动结束后，获取地图的可视区坐标范围
      const bounds = this.map.getBounds();
      // 获取地图可视区域四个角的经纬度坐标
      const northEast = bounds.getNorthEast();
      const southEast = new BMap.Point(
        northEast.lng,
        bounds.getSouthWest().lat
      );
      const northWest = new BMap.Point(
        bounds.getSouthWest().lng,
        northEast.lat
      );
      const southWest = bounds.getSouthWest();
      console.log("东北角的坐标：", northEast);
      console.log("东南角的坐标：", southEast);
      console.log("西北角的坐标：", northWest);
      console.log("西南角的坐标：", southWest);
      this.visibleAreaPoints = { northEast, southEast, northWest, southWest };
    },

    /**
     * @Event 方法
     * @description: 获取可视区坐标 --- 按钮点击
     * */
    getVisibleAreaPointsBtn() {
      this.getVisibleAreaPoints();
      this.$message.success(JSON.stringify(this.visibleAreaPoints));
    },

    /**
     * @Event 方法
     * @description: 关闭消息窗体
     * */
    infoWindowClose() {
      this.showInfoWindow = false;
    },

    /**
     * @Event 方法
     * @description: 打开消息窗体
     * */
    infoWindowOpen() {
      console.log(1);
      this.showInfoWindow = true;
    },
  },
  created() {
    this.getPoints();
  },
  mounted() {},
};
</script>

<style lang="scss" scoped>
.map {
  width: 100%;
  height: 100%;
}
</style>
